import React from 'react';

/**
 * A piece to show off, with an image and some text
 */
export default class Showpiece extends React.Component {
  render() {
    const { name, thumbnail, description, url } = this.props;

    return (
      <a href={url}>
        <div className='showpiece'>
          <img src={thumbnail} alt={name}/>
          <h3>{name}</h3>
          <p>{description}</p>
        </div>
      </a>
    );
  }
}
